import React from 'react';
import { useLanguage } from '../context/LanguageContext';
import '../styles/LanguageSelector.css';

const LanguageSelector = () => {
  const { language, changeLanguage } = useLanguage();

const languages = [
    { value: 'es', label: 'Espaol' },
    { value: 'eus', label: 'Euskera' },
    { value: 'en', label: 'Ingls' },
    { value: 'fr', label: 'Francs' },
    { value: 'de', label: 'Alemn' },
    { value: 'pt', label: 'Portugus' },
    { value: 'zh-CN', label: 'Chino simplificado' },
    { value: 'zh-TW', label: 'Chino tradicional' },
    { value: 'it', label: 'Italiano' },
    { value: 'cat', label: 'Cataln' },
    { value: 'gal', label: 'Gallego' },
    { value: 'ja', label: 'Japons' }, // <-- coma aadida
    { value: 'no', label: 'Noruego' },
    { value: 'ru', label: 'Ruso' },
    { value: 'ar', label: 'rabe' },
];

  return (
    <div className="language-selector">
      <select 
        id="language-selector"
        value={language}
        onChange={(e) => changeLanguage(e.target.value)}
        className="language-select"
      >
        {languages.map((lang) => (
          <option key={lang.value} value={lang.value}>
            {lang.label}
          </option>
        ))}
      </select>
    </div>
  );
};

export default LanguageSelector;
